import React, { Component } from 'react';

class Table extends Component {
  findStudentTask(name, number, students, dataTasks) {
    const fulldata = [];
    let color;
    for (let i = 0; i < dataTasks.length; i += 1) {
      switch (dataTasks[i].status) {
        case 'ToDo':
          color = 'gray';
          break;
        case 'Checking':
          color = 'pink';
          break;
        case 'In Progress':
          color = 'yellow';
          break;
        case 'Checked':
          color = 'red';
          break;
        default:
          color = 'white';
          break;
      }

      fulldata.push({
        name: dataTasks[i].name,
        status: dataTasks[i].status,
        score: color,
      });
    }

    let findStudent = null;
    for (let i = 0; i < students.length; i += 1) {
      if (students[i].name === name) {
        findStudent = students[i];
        break;
      }
    }

    if (findStudent !== null) {
      for (let i = 0; i < fulldata.length; i += 1) {
        for (let j = 0; j < findStudent.tasks.length; j += 1) {
          if (fulldata[i].name === findStudent.tasks[j].task) {
            fulldata[i].score = 'green';
          }
        }
      }
    }

    return fulldata;
  }

  createTable(tasks, students) {
    let studentTask;

    studentTask = students.map((data, i) => {
      return this.findStudentTask(
        data,
        i,
        this.props.dataStudents,
        this.props.dataTasks
      );
    });

    if (students.length === 0) {
      return null;
    }

    return tasks.map((data, i) => {
      return (
        <tr key={i}>
          <td>
            <a href={data.http}>{data.name}</a>
          </td>
          {studentTask.map((_data, j) => {
            return <td key={j} className={`${_data[i].score}`} />;
          })}
        </tr>
      );
    });
  }

  createThead(tasks) {
    if (tasks.length === 0) {
      return null;
    }
    return (
      <tr>
        <th />
        {tasks.map((data, i) => {
          return <th key={i}>{data}</th>;
        })}
      </tr>
    );
  }

  render() {
    return (
      <table>
        <thead>{this.createThead(this.props.students)}</thead>
        <tbody>{this.createTable(this.props.tasks, this.props.students)}</tbody>
      </table>
    );
  }
}

export default Table;
