import React, { Component } from 'react';
import Data from './data.json';
import SearchForm from './SearchForm';
import './App.css';
import Auth from './Auth';
import Table from './Table';

class App extends Component {
  constructor() {
    super();
    this.state = {
      mentor: 'Mentor',
      students: [],
      tasks: [
        {
          name: '',
        },
      ],
      visible: false,
      logined: null,
    };

    this.onChangeNewMentor = this.onChangeNewMentor.bind(this);
    this.loginGithub = this.loginGithub.bind(this);
    this.unLoginInGithub = this.unLoginInGithub.bind(this);
    this.loginInGithub = this.loginInGithub.bind(this);
  }

  storageMentor() {
    let mentor = localStorage.getItem('mentor-dashboard');
    this.onChangeNewMentor(mentor);
  }

  init() {
    this.setState({
      tasks: Data.tasks,
    });
  }

  componentWillMount() {
    this.storageMentor();
  }

  loginInGithub(mentor) {
    this.setState({
      logined: mentor,
    });
  }

  unLoginInGithub() {
    this.setState({
      logined: null,
    });
  }

  onChangeNewMentor(newMentor, logined) {
    const mentorStudents = this.findStudent(newMentor, Data);
    this.setState({
      mentor: newMentor,
      students: mentorStudents,
    });
    localStorage.setItem('mentor-dashboard', newMentor);
    this.init();
    if (logined) {
      this.loginInGithub(newMentor);
    }
  }

  findStudent(mentorValue, Data) {
    let indexFlag;
    let students = [];
    indexFlag = Data.mentors.map(e => e.git).indexOf(mentorValue);
    if (indexFlag !== -1) {
      students = Data.mentors[indexFlag].students;
    }
    return students;
  }

  loginGithub() {
    if (this.state.visible) {
      this.setState({
        visible: false,
      });
    } else {
      this.setState({
        visible: true,
      });
    }
  }

  render() {
    return (
      <div>
        <SearchForm data={Data.mentors} changeMentor={this.onChangeNewMentor} />
        <button onClick={this.loginGithub}>Login</button>
        {this.state.visible ? (
          <Auth
            login={this.state.logined}
            changeMentor={this.onChangeNewMentor}
            unLogin={this.unLoginInGithub}
          />
        ) : null}
        <div>Mentor: {this.state.mentor}</div>
        <Table
          students={this.state.students}
          tasks={this.state.tasks}
          dataStudents={Data.students}
          dataTasks={Data.tasks}
        />
      </div>
    );
  }
}

export default App;
