import React, { Component } from 'react';
import './Auth.css';
const GitHub = require('github-api');

class Auth extends Component {
  constructor(props) {
    super(props);
    this.state = {
      login: '',
      pass: '',
      enter: false,
    };
    this.handleLoginChange = this.handleLoginChange.bind(this);
    this.handlePassChange = this.handlePassChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleExit = this.handleExit.bind(this);
  }

  handleLoginChange(event) {
    this.setState({ login: event.target.value });
  }

  handlePassChange(event) {
    this.setState({ pass: event.target.value });
  }

  handleExit() {
    this.setState({
      login: '',
      pass: '',
      enter: false,
    });
    this.props.unLogin();
  }

  handleSubmit(event) {
    event.preventDefault();

    const gh = new GitHub({
      username: this.state.login,
      password: this.state.pass,
    });

    const me = gh.getUser();

    let cb;
    me.getProfile(cb).then(
      result => {
        console.log('welcome: ', result.data.login);
        this.setState({
          login: result.data.login,
          pass: '',
          enter: true,
        });
        console.log('result.data.login', result.data.login);
        this.props.changeMentor(result.data.login, true);
      },
      error => {
        console.log('Rejected: ' + error);
        this.setState({
          login: '',
          pass: '',
          enter: false,
        });
      }
    );
  }

  paintForm() {
    return (
      <div className="form-auth">
        <form onSubmit={this.handleSubmit}>
          <div className="container">
            <label>
              <b>Username</b>
            </label>
            <input
              type="text"
              placeholder="Enter Username"
              name="uname"
              value={this.state.login}
              onChange={this.handleLoginChange}
              required
            />

            <label>
              <b>Password</b>
            </label>
            <input
              type="password"
              placeholder="Enter Password"
              name="psw"
              value={this.state.pass}
              onChange={this.handlePassChange}
              required
            />

            <button type="submit">Login</button>
          </div>
        </form>
      </div>
    );
  }

  componentWillMount() {
    if (this.props.login !== null) {
      this.setState({
        login: this.props.login,
        enter: true,
      });
    }
  }

  paintCabinet() {
    return (
      <div className="container">
        <label>
          <b>{'Welcome ' + this.state.login + ' !'}</b>
        </label>
        <br />
        <button onClick={this.handleExit}>Exit</button>
      </div>
    );
  }

  render() {
    return (
      <div>
        <div>{this.state.enter ? this.paintCabinet() : this.paintForm()}</div>
      </div>
    );
  }
}

export default Auth;
