import React, { Component } from 'react';
import Select from 'react-select';

class SearchForm extends Component {
  constructor(props) {
    super(props);
    this.options = [];
    this.state = {
      mentorSelect: 'mentor',
    };
    this.data = this.props.data;
    this.options = this.createOptions(this.data);
  }

  createOptions(data) {
    const options = data.map(item => {
      return {
        value: item.git,
        label: item.name + ' <' + item.git + '>',
      };
    });
    return options;
  }

  updateState(newValue) {
    this.setState({ mentorSelect: newValue });
    this.props.changeMentor(newValue.value, false);
  }

  render() {
    return (
      <div>
        <Select
          value={this.state.mentorSelect}
          placeholder="Select a mentor"
          onChange={this.updateState.bind(this)}
          options={this.options}
        />
      </div>
    );
  }
}

export default SearchForm;
